.main-section {
  padding-top: 120px;
  padding-bottom: 120px;

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 58px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 71px;
  }

  &--flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

    @include vp-767 {
      padding-top: 54px;
      padding-bottom: 70px;
    }
  }

  &--vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 141px;
    position: relative;

    @include vp-1199 {
      padding-top: 114px;
    }

    @include vp-767 {
      padding-top: 71px;
      padding-bottom: 72px;
    }
  }

  &--coach-swiper {
    padding-top: 0;
    padding-bottom: 104px;
  }

  &--event {
    padding-top: 156px;

    @include vp-1199 {
      padding-top: 105px;
    }

    @include vp-767 {
      padding-top: 70px;
    }
  }

  &--contacts {
    padding-top: 0;
    padding-bottom: 0;

    @include vp-1199 {
      flex-direction: column;
    }
  }

  &--reviews {
    padding-top: 139px;
    padding-bottom: 119px;

    @include vp-1199 {
      padding-top: 103px;
      padding-bottom: 98px;
    }

    @include vp-767 {
      padding-top: 73px;
      padding-bottom: 70px;
    }
  }

  &--gym {
    @media (min-width: $vp-1200) {
      display: grid;
      grid-template-columns: 36.21% 1fr 56.9%;
      column-gap: 17px;

      & .main-section__gim-description {
        width: 100%;
      }

      & .main-section__gim-img {
        grid-column-start: 3;
        grid-row-start: 1;
        width: 100%;
      }

      & .main-section__gim-video-wrapper {
        grid-column: 2/4;
        grid-row-start: 2;
        padding: 0;
        flex-direction: row;
      }

      & .main-section__gim-video-conatainer {
        height: auto;
        width: auto;
        justify-content: start;
        max-width: 500px;
      }
    }
  }
}

.main-section h2 {
  color: $color-sapphire;
  font-family: "Alumni Sans", "arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  margin: 0;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.main-section--reviews h2 {
  margin-bottom: 16px;

  @include vp-1199 {
    margin-bottom: 12px;
  }

  @include vp-767 {
    margin-bottom: 20px;
  }
}

.main-section--coach-swiper h2 {
  line-height: 66%;
  margin-bottom: 62px;
  margin-top: 1px;
  padding-left: 2px;

  @include vp-1199 {
    margin-bottom: 41px;
  }

  @include vp-767 {
    margin-bottom: 50px;
  }
}


.main-section__gim-description h2 {
  font-family: "Alumni Sans", "arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  color: $color-sapphire;
  margin: 0;
  margin-top: 39px;
  width: max-content;
  letter-spacing: 1.1px;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
    margin-bottom: -13px;
  }

  @include vp-767 {
    margin-top: 0;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.main-section__gim-description {
  min-width: 355px;
  padding-left: 60px;
  padding-right: 0;
  width: 36.21%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 0;
  grid-row: 1/3;

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    // padding-right: 11px;
    margin: 0 auto;
    width: 88.5%;
    padding-left: 5px;
  }

  @include vp-767 {
    min-width: 0;
    align-items: flex-start;
    padding: 0 15px;
    width: 100%;
  }
}


.main-section__gim-description span {
  font-family: "Roboto Condensed", "arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
  color: $color-sapphire;

  @include vp-1199 {
    margin-left: -3px;
  }

  @include vp-767 {
    margin: 0;
  }
}

.main-section__gim-description p {
  color: $color-sapphire;
  font-family: "Roboto Condensed", "arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 150%;
  margin-bottom: 8px;
}

.main-section__desctop-only {
  @include vp-1199 {
    display: none;
  }
}

.main-section__desctop-only p {
  margin: 0;
}

.main-section__gim-video-wrapper {
  padding-right: 30.95%;
  flex-grow: 1;
  display: flex;
  flex-direction: row-reverse;
  margin-top: -113px;

  @include vp-1199 {
    padding: 0;
    width: 100%;
    justify-content: center;
    margin-top: -84px;
  }

  @include vp-767 {
    margin: 0;
    margin-top: 40px;
  }
}

.main-section__gim-video-conatainer {
  position: relative;
  width: 26.65vw;
  height: 16.69vw;
  display: flex;
  align-items: center;
  justify-content: center;

  @include vp-1199 {
    width: 34.64vw;
    height: auto;
  }

  @include vp-767 {
    width: 100vw;
    height: 52.19vw;
  }
}

.main-section__gim-video-conatainer img {
  width: 26.65vw;
  height: auto;

  @include vp-1199 {
    width: 34.64vw;
  }

  @include vp-767 {
    width: 100vw;
    min-width: 320px;
  }
}

.main-section__play {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(20, 54, 122, 0.50);

  @include vp-767 {
    height: 50.5vw;
  }
}

.main-section__play button {
  position: relative;
  width: 72px;
  height: 72px;
  background: $btn-gradient;
  border-radius: 50%;
  padding: 0;
  border: none;
  color: $color-default-white;

  @include vp-1199 {
    width: 52px;
    height: 52px;
  }

  @include vp-767 {
    margin-top: 5px;
    margin-left: 2px;
  }

  &::before {
    content: url("../img/svg/button-border.svg");
    position: absolute;
    display: block;
    top: -14px;
    left: -14px;
    bottom: -14px;
    right: -14px;
    width: 100px;
    height: 100px;
    background-color: transparent;
    cursor: pointer;
    box-sizing: border-box;

    @include vp-1199 {
      width: 73px;
      height: 73px;
      top: -11px;
      left: -11px;
      bottom: -11px;
      right: -11px;
    }
  }

  @include hover {
    background: $bg-default-white;
    color: $color-sapphire;

    &::before {
      content: "";
      border: 2px solid $color-sapphire;
      border-radius: 50%;
    }
  }

  @include focus {
    background: $bg-default-white;
    color: $color-sapphire;
    outline: none;

    &::before {
      content: "";
      border: 2px solid $color-sapphire;
      border-radius: 50%;
    }
  }

  .main-section__play svg {
    display: block;
    width: 17px;
    height: 17px;

    @include vp-767 {
      width: 12px;
      height: 12px;
    }
  }

  &:active {
    width: 58px;
    height: 58px;
    background: $bg-default-white;
    color: $color-sapphire;
    outline: none;

    &::before {
      content: "";
      border: 2px solid $color-sapphire;
      border-radius: 50%;
      top: -21px;
      left: -21px;
    }
  }

  &:disabled {
    color: $color-sapphire;
    background: $bg-default-white;
    opacity: 0.4;

    &::before {
      content: "";
      border: 2px solid $color-sapphire;
      border-radius: 50%;
    }
  }
}

.main-section__gim-video-conatainer iframe {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.main-section__video {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
}

.main-section__gim-text {
  margin-top: 14px;

  @include vp-1199 {
    width: 511px;
    margin-top: 9px;
    text-align: center;
  }

  @include vp-767 {
    width: 81.25vw;
    text-align: left;
    margin-top: 0;
  }
}

.main-sectuin__gim-text p {
  @include vp-1199 {
    text-align: center;
  }

  @include vp-767 {
    text-align: left;
    margin-top: 8px;
  }
}

.main-section__gim-img img {
  display: block;
  object-fit: cover;
  width: 100%;

  @include vp-1199 {
    width: 100%;
    height: 100%;
  }

  @include vp-767 {
    width: 100vw;
    height: auto;
  }
}

.main-section__gim-img {
  width: 56.9%;
  padding-left: 0;
  padding-right: 0;
  margin-right: 0;

  @include vp-1199 {
    margin: 0;
    margin-top: 36px;
    width: 100%;
    padding-left: 0;
  }

  @include vp-767 {
    margin: 0;
    margin-top: 25px;
    width: 100%;
  }
}

.main-section__play svg {
  width: 11px;
  height: 11px;
}
