.container {
  width: 100%;
  max-width: 1900px;
  margin: 0 auto;
  padding: 0 103px;

  @include vp-1199 {
    padding: 0 45px;
  }

  @include vp-767 {
    padding: 0 15px;
  }

  &--no-mobile {
    @include vp-767 {
      width: 100vw;
      padding: 0;
      box-sizing: border-box;
    }
  }
}
